import btnMixin from "./btn-mixin";
var COMPONENT_NAME = 'cube-upload-btn';
export default {
  name: COMPONENT_NAME,
  mixins: [btnMixin],
  methods: {
    changeHandler: function changeHandler(e) {
      var fileEle = e.currentTarget;
      var files = fileEle.files;

      if (files) {
        this.$parent.addFiles(files);
        fileEle.value = null;
      }
    }
  }
};