import "core-js/modules/web.dom.iterable";
import {
/* eslint-disable no-unused-vars */
Style // locale
, Locale // basic
, Button, Loading, Tip, Toolbar, TabBar, TabPanels // form
, Checkbox, CheckboxGroup, Checker, Radio, RadioGroup, Input, Textarea, Select, Switch, Rate, Validator, Upload, Form // popup
, Popup, Toast, Picker, CascadePicker, DatePicker, TimePicker, SegmentPicker, Dialog, ActionSheet, Drawer, ImagePreview // scroll
, Scroll, Slide, IndexList, Swipe, Sticky, ScrollNav, ScrollNavBar, RecycleList // module
, BetterScroll, createAPI } from "./module";
import { processComponentName } from "./common/helpers/util";
var components = [// locale
Locale, // basic
Button, Loading, Tip, Toolbar, TabBar, TabPanels, // form
Checkbox, CheckboxGroup, Checker, Radio, RadioGroup, Input, Textarea, Select, Switch, Rate, Validator, Upload, Form, // popup
Popup, Toast, Picker, CascadePicker, DatePicker, TimePicker, SegmentPicker, Dialog, ActionSheet, Drawer, ImagePreview, // scroll
Scroll, Slide, IndexList, Swipe, Sticky, ScrollNav, ScrollNavBar, RecycleList];

function install(Vue) {
  if (install.installed) {
    return;
  }

  install.installed = true;
  components.forEach(function (Component) {
    // ignore radio
    if (Component === Radio) {
      return;
    }

    Component.install(Vue);
  });
}

var Cube = {
  /* eslint-disable no-undef */
  version: '1.12.30',
  install: install,
  BScroll: BetterScroll,
  createAPI: createAPI
};
components.forEach(function (Component) {
  var name = processComponentName(Component, {
    firstUpperCase: true
  });
  Cube[name] = Component;
});

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(install);
}

export default Cube;