var COMPONENT_NAME = 'cube-switch';
var EVENT_INPUT = 'input';
export default {
  name: COMPONENT_NAME,
  props: {
    value: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      checkboxValue: this.value
    };
  },
  watch: {
    value: function value(newVal) {
      this.checkboxValue = newVal;
    },
    checkboxValue: function checkboxValue(newVal) {
      this.$emit(EVENT_INPUT, newVal);
    }
  }
};