var COMPONENT_NAME = 'cube-button';
export default {
  name: COMPONENT_NAME,
  props: {
    icon: {
      type: String,
      default: ''
    },
    active: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    inline: {
      type: Boolean,
      default: false
    },
    primary: {
      type: Boolean,
      default: false
    },
    outline: {
      type: Boolean,
      default: false
    },
    light: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'button'
    }
  },
  computed: {
    btnClass: function btnClass() {
      return {
        'cube-btn_active': this.active,
        'cube-btn_disabled': this.disabled,
        'cube-btn-inline': this.inline,
        'cube-btn-primary': this.primary,
        'cube-btn-outline': this.outline,
        'cube-btn-outline-primary': this.outline && this.primary,
        'cube-btn-light': this.light
      };
    }
  },
  methods: {
    handleClick: function handleClick(event) {
      if (this.disabled) {
        event.preventDefault();
        event.stopPropagation();
        return;
      }

      this.$emit('click', event);
    }
  }
};